/**
 *   api
 *  @module base/api/forms
 *  @author coder
 */

// API_HOST
import {
  API_HOST
} from '@/config'

// ajax
import ajax from '@/utils/ajax'

/**
 * 获取forms列表
 * @const
 * @type {String}
 * @default  '[API_HOST]/forms'
 */
export const FETCH_FORMS = API_HOST + '/forms'

/**
 * 获取forms列表
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function fetchForms(data) {
  return ajax({
    method: 'get',
    data: data,
    url: FETCH_FORMS
  })
}

/**
 * 获取forms单条记录
 * @const
 * @type {String}
 * @default  '[API_HOST]/forms/:id'
 */
export const GET_FORMS = API_HOST + '/forms/:id'

/**
 * 获取forms单条记录
 * @param {string|number} id Url参数id
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function getForms(id, data) {
  return ajax({
    method: 'get',
    params: {
      id
    },
    data: data,
    url: GET_FORMS
  })
}

/**
 * 新增forms
 * @const
 * @type {String}
 * @default  '[API_HOST]/forms'
 */
export const ADD_FORMS = API_HOST + '/forms'

/**
 * 新增forms
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function addForms(data) {
  return ajax({
    method: 'post',
    data: data,
    url: ADD_FORMS
  })
}

/**
 * 更新forms
 * @const
 * @type {String}
 * @default  '[API_HOST]/forms'
 */
export const UPDATE_FORMS = API_HOST + '/forms'

/**
 * 更新forms
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function updateForms(data) {
  return ajax({
    method: 'patch',
    data: data,
    url: UPDATE_FORMS
  })
}

/**
 * 删除forms
 * @const
 * @type {String}
 * @default  '[API_HOST]/forms/:id'
 */
export const REMOVE_FORMS = API_HOST + '/forms/:id'

/**
 * 删除forms
 * @param {string|number} id Url参数id
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function removeForms(id, data) {
  return ajax({
    method: 'delete',
    params: {
      id
    },
    data: data,
    url: REMOVE_FORMS
  })
}

/**
 * 批量删除forms
 * @const
 * @type {String}
 * @default  '[API_HOST]/forms/remove'
 */
export const BATCH_FORMS = API_HOST + '/forms/remove'

/**
 * 批量删除forms
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function batchForms(data) {
  return ajax({
    method: 'post',
    data: data,
    url: BATCH_FORMS
  })
}
